<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" icon="el-icon-plus" @click="add()">新增</el-button>
      </div>
      <el-table :data="tableData" border class="table" id="multipleTable">
        <el-table-column label="序号">
          <template slot-scope="scope">
            <span>{{scope.$index+(cur_page-1)*cur_size+1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="标题" prop="name">
        </el-table-column>
        <el-table-column label="操作" width="280" align="center">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-document" @click="handleDetails(scope.$index, scope.row)">详情
            </el-button>
            <!--            <el-button type="text" icon="el-icon-document" @click="goVs(scope.$index, scope.row)">车系-->
            <!--            </el-button>-->
            <el-button type="text" icon="el-icon-edit" @click="handleI(scope.$index, scope.row)">编辑</el-button>
            <el-button type="text" icon="el-icon-setting" class="red" @click="open(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
  import http from '../../../assets/js/http';

  export default {
    name: 'ruzhi',
    data() {
      return {
        tableData: [],
        total: 0,
        cur_page: 1,
        cur_size: 10,
        formAdd:{},
      }
    },
    created() {
      this.getCategoryData();
    },
    computed: {},
    mounted() {

    },
    methods: {
      // 分页导航
      handleCurrentChange(val) {
        this.cur_page = val;
        this.getCategoryData();
      },
      handleSizeChange(val) {
        this.cur_size = val;
        this.getCategoryData();
      },
      getCategoryData() { //
        let that = this;
        const data = {
          "list_rows": parseInt(that.cur_size),
          "page": parseInt(that.cur_page),
          fcolumn:7
        };
        that.apiPost('/admin/djlmbyf', data).then((res) => {
          that.handelResponse(res, (data) => {
            that.total = data.data.total;
            that.tableData = data.data.data;
          })
        })
      },
      add() {
        var that= this;
        this.$prompt('请输入分类', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          var param = {
            name:value,
            fcolumn:7,
            url:'/home/chickservicecheckup'
          };
          if (value==null){
            that.$message.error('请输入分类');
            return false;
          }
          that.apiPost('/admin/djlmadd',param).then((res)=>{
            that.handelResponse(res,(data)=>{
              that.$message.success('新增成功');
              that.getCategoryData()
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      },
      //详情
      handleDetails(index, row) {
        this.$router.push({
          path: '/type',
          query:{
            id:row.id
          }
        });
      },
      //编辑
      handleI(index, row) {
        var that= this;
        this.$prompt('请输入分类', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue:row.name
        }).then(({ value }) => {
          var param = {
            name:value,
            fcolumn:row.fcolumn,
            datoken:row.datoken,
          };
          if (value==''){
            that.$message.error('请输入分类');
            return false;
          }
          that.apiPost('/admin/djlmupdate',param).then((res)=>{
            that.handelResponse(res,(data)=>{
              that.$message.success('修改成功');
              that.getCategoryData()
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      },
      //删除
      open(index, row) {
        var that = this;
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let param = {
            datoken: row.datoken,
          };
          this.apiPost('/admin/djlmdelete', param).then((res) => {
            this.handelResponse(res, (data) => {
              this.$message.success('成功');
              this.getCategoryData()
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    mixins: [http]
  }
</script>

<style scoped>
  img {
    width: 100px;
    height: 100px;
  }
  .handle-box {
    margin-bottom: 20px;
  }
  .del-dialog-cnt {
    font-size: 16px;
    text-align: center
  }
  .table {
    width: 100%;
    font-size: 14px;
  }
  .red {
    color: #ff0000;
  }
  .handle-input {
    width: 300px;
  }
  .el-radio {
    margin-bottom: 20px;
  }
</style>



